.navbar-nav {
  padding-left: 20px;
  border-left: 1px solid #515151;

  .nav-item {
    margin: 0;

    .nav-link {
      text-transform: capitalize;
      color: $text-color;
      padding: 5px 0;
      font-size: 16px;
      font-family: $secondary-font;

      &.active {
        color: $white;
      }
    }
  }
}

.navbar-toggler {
  display: none;

  @include tablet {
    display: block;
  }
}

.main-wrapper {
  display: flex;

  @include tablet {
    overflow-x: hidden;
  }
}

.wrapper {
  width: calc(100% - 340px);
  transition: .2s ease;
  position: relative;

  &-bg {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    max-width: 80%;
  }

  @include desktop-lg {
    width: calc(100% - 250px);
  }

  @include tablet {
    width: 100%;
    padding-top: 40px;
  }

  &.hide {
    transform: translateX(250px);
  }
}

.sidenav {
  position: sticky;
  top: 0;
  z-index: 9;
  min-width: 340px;
  min-height: 100vh;
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: .2s ease;

  @include desktop-lg {
    min-width: 250px;
    padding: 60px 20px;
  }

  @include tablet {
    position: fixed;
    padding: 40px 20px;
    background-color: $body-color;
    left: -300px;
  }

  &.show {
    @include tablet {
      left: 0;
    }
  }
}

.video {
  position: relative;

  img {
    border-radius: 15px;
    width: 100%;
  }

  &-btn {
    height: 45px;
    width: 45px;
    line-height: 45px;
    background-color: $primary-color;
    border-radius: 50%;
    display: block;
    position: absolute;
    text-align: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: $white;
    box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.2);
    transition: .2s ease;

    &:hover {
      box-shadow: 0 0 0 15px rgba(255, 255, 255, 0.3);
      color: $white;
      text-decoration: none;
    }
  }
}

// block
.block {
  background-color: $white;
  border-radius: 30px;
  padding: 40px;
  margin: 30px;

  &-title {
    margin-bottom: 30px;
    text-align: center;
  }
}

// default slider
.logo-slider {
  .slick-track {
    display: flex;
    align-items: center;
    a{
      display: block;
      text-align: center;
      padding: 10px 25px;
      img{
        display: initial;
        max-width: 100%;
        height: auto;
      }
    }
  }
  .slick-dots{
    padding: 0;
    text-align: center;
    li{
      display: inline-block;
      margin: 5px;
      &.slick-active{
        button{
          background-color: $primary-color;
          border-color: $primary-color;
        }
      }
      button{
        color: transparent;
        padding: 0;
        background-color: transparent;
        border: 1px solid $border-color;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        &:focus{
          outline: 0;
        }
      }
    }
  }
  
  .slick-arrow{
    position: absolute;
    background-color: transparent;
    border: 1px solid $border-color;
    border-radius: 30px;
    height: 25px;
    line-height: 25px;
    width: 40px;
    bottom: -30px;
    transition: .2s ease;
    font-size: 10px;
    &:focus{
      outline: 0;
    }
    &.arrow-prev{
      left: calc(50% - 50px);
    }
    &.arrow-next{
      right: calc(50% - 50px);
    }
    &:hover{
      color: $white;
      background-color: $primary-color;
      border-color: $primary-color;
    }
  }
}
